nav {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 49px;
  background: #47F7A0; }
  nav li {
    display: block;
    position: relative;
    height: 100%;
    width: 25%;
    float: left; }
    nav li a {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 100%;
      text-indent: 9999px;
      white-space: nowrap;
      overflow: hidden; }
      nav li a svg {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 80%;
        height: 80%;
        transform: translateX(-50%) translateY(-50%); }
